// get built in
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from "@angular/router";

// get components
import { Item } from '../model/item.model';
import { ItemService } from '../service/item.service';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent implements OnInit {

  showSucessMessage: boolean;
  serverErrorMessages: string;
  auctionName: string;

  constructor(
    private itemService: ItemService,
    private activatedRoute: ActivatedRoute)
    {
      // Get the param value
      this.activatedRoute.queryParams.subscribe(params => {
      this.auctionName = params["auctionName"];
      })
    }

  item: Item = {
    auctionName: this.auctionName,
    itemCode: '',
    itemName: '',
    description: '',
    price: 0,
    quantity: 0,
    winner: ''
  }

  ngOnInit() {
    this.item.auctionName = this.auctionName;
  }

  onSubmit(form: NgForm) {
    this.item.auctionName = this.auctionName;
    this.item.itemCode = form.value.itemCode;

    this.itemService.addItem(form.value).subscribe(
      res => {
        this.showSucessMessage = true;
        setTimeout(() => this.showSucessMessage = false, 4000);
        this.resetForm(form);
      },
      err => {
        if (err.status === 422) {
          this.serverErrorMessages = err.error.join('<br/>');
        }
        else
          this.serverErrorMessages = 'Something went wrong.Please contact admin.';
      }
    );
  }

  resetForm(form: NgForm) {
    console.log(this.auctionName);
    this.item = {
      auctionName: this.auctionName,
      itemCode: '',
      itemName: '',
      description: '',
      price: 0,
      quantity: 0,
      winner: '',
    };

    form.resetForm();
    this.serverErrorMessages = '';
  }
}
